import react from "react"
import { Link } from "react-router-dom";

const Home = ()=>{

    return (
      <div className="card home card-widget">
        <div className="card-header">
          <h1 className="white title">Offre de starge</h1>
        </div>
        <div className="card-content">
          <p className="white subtitle">
            A l'occasion du lancement officiel de ses activités à Douala, Guihon
            groupe sarl recrute des jeunes sérieux, dynamiques et perceverants
            sans distinction de sexe.
          </p>
          <p className="white subtitle important">
            <strong className="badge">NB:</strong> Une compétence en Economie,
            Gestion et Commerce serait un atout.
          </p>
        </div>
        <div className="card-footer">
          <Link
            to="/register"
            className="btn btn-primary-light btn-normal mt-10"
          >
            Effectuer une demande
          </Link>
        </div>
      </div>
    );
}

export default Home;
